// src/hooks/useDynamicParams.ts
import { useParams } from 'react-router-dom';

/**
 * Ez a hook lehetv teszi a paramterek dinamikus lekrdezst
 * akkor is, ha dinamikusan importlt komponensekben hasznljuk.
 */
const useDynamicParams = () => {
  const params = useParams(); // Lekrdezi a route paramtereket a React Router-bl
  return params; // Visszaadja a paramtereket, mint objektumot
};

export default useDynamicParams;
